.phone {
    text-decoration: none;
    background-color: black;
    font-size: 30px;
    color: #000000;
    border-radius: 24px;
    
    -webkit-animation: glowing 2300ms infinite;
      -moz-animation: glowing 2300ms infinite;
      -o-animation: glowing 2300ms infinite;
      animation: glowing 2300ms infinite;
}

@-webkit-keyframes glowing {
    /* 0% { background-color: #de1010; -webkit-box-shadow: 0 0 3px #77b200; }
    50% { background-color: #024ee7; -webkit-box-shadow: 0 0 15px #a6a5aa; }
    100% { background-color: #b27100; -webkit-box-shadow: 0 0 3px #0091b2; } */
    }
    @keyframes glowing {
    50% { background-color: #f74600; box-shadow: 0 0 3px #0091b2; }
    70% { background-color: #0824f8; box-shadow: 0 0 15px #b80ea1; }
    100% { background-color: #b20000; box-shadow: 0 0 3px #ff0505; }
    }


